body {
  margin: 0 10rem !important;
}

.fa {
  padding: 20px;
  font-size: 40px !important;
  text-align: center;
  margin: 10px;
  border-radius: 50%;
  text-decoration: none;
  color: #0f529b !important;
}

.fa-github-alt,
.fa-linkedin {
  font-size: 50px;
}

.far {
  padding: 0px !important;
}

.text-primary {
  color: rgb(231, 229, 229) !important;
}

.bg-dark {
  --bs-bg-opacity: 0.9;
  background-color: #181818 !important;
}

.text-danger {
  color: #740f53 !important;
}

.text-info {
  color: #8cd98c !important;
}

.btn {
  border-radius: 1000px;
  color: #8cd98c !important;
  background: #0f529b !important;
  border: none;
  padding: 10px 25px;
  margin-right: 20px;
}

.bg-primary {
  background-color: #0f529b !important;
}

.btn:hover {
  opacity: 0.7;
}

a:hover {
  color: #8cd98c !important;
}

p a {
  color: #740f53 !important;
  text-decoration: none;
}

.about {
  margin-right: 3rem;
}

.resume {
  color: #8cd98c !important;
  text-decoration: none;
  font-size: 1.5rem;
  font-style: italic;
}

img {
  opacity: 80%;
}

.img-fluid {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: #740f53;
}

.homeimg {
  width: 100%;
}

@media only screen and (min-device-width: 668px) and (max-device-width: 991.98px) {
  body {
    margin: 1rem 3rem !important;
  }
  .homeimg {
    width: 100%;
    padding-top: 4rem;
  }
  .navbar {
    margin-left: 10px;
  }
}

@media only screen and (min-device-width: 375px) and (max-device-width: 667px) {
  body {
    margin: 0 !important;
  }
  .homeimg {
    width: 100%;
    padding-top: 4rem;
  }
  .navbar {
    margin-left: 10px;
  }
  .homepage {
    text-align: center;
  }
} ;
